@use "./scss/" as *;

@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Ubuntu:wght@300;400;500;700&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
}

html {
    font-size: 6.25%; // 16px -> 1rem = 1 px

    @include tablet {
        font-size: 5%; // 12.8px 
    }

    @include mobile {
        font-size: 3.75%; // 9.6px
    }
}

body {
    font-family: $font-family;
    font-weight: 400;
    line-height: 1.5;

    background-color: $body-bg;
    color: $txt-color;
}

a {
    text-decoration: none;
    color: unset;
}

@media (hover: hover) {
    a:hover {
        color: $main-color;
    }
}

button,
input {
    outline: 0;
}

ul {
    list-style-type: none;
}

img {
    max-width: 100%;
}

iframe {
    border: 0;
}

.container {
    max-width: 1090px;
    margin: auto;
}

.max-center {
    max-width: 1050px;
    margin: 0 auto;
}

.mb-3 {
    margin-bottom: 3rem;
}

.mb-2 {
    margin-bottom: 2rem;
}

.mt-30 {
    margin-top: 30rem;
}

.section {
    padding: 0 2rem;
}

.padding-page {
    padding: 25rem;
    padding-top: 110rem;
    
    @include mobile {
     padding-top: 150rem;
    }
}
