@use "../../scss/" as *;

.movie-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 20rem;
  @include tablet {
      grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  }

  &__loadmore {
      text-align: center;
  }
}

.movie-search {
  position: relative;
  width: 100%;
  max-width: 500px;

  input {
      width: 100%;
      padding-right: 8rem;
  }

  button {
      position: absolute;
      right: 0;
      top: 2px;

      @include mobile {
          right: 1px;
          top: 1px;
      }
  }
}
.movie-grid__pagination {
    .MuiPagination-root {
        .MuiButtonBase-root.MuiPaginationItem-page:hover{
            color: #E13C2F;
            border: 1px solid rgba(225, 60, 47, 0.5);
            background-color: rgba(225, 60, 47, 0.12);
        }
    }
}

.MuiCircularProgress-root{
    .MuiCircularProgress-root.MuiCircularProgress-indeterminate {
        display: flex;
        justify-content: center;
        margin: 30px auto;
    }
}
